import React, {useState,useEffect} from 'react';
import {Link, useHistory, useLocation} from 'react-router-dom';
import {AppBar, Typography, Toolbar, Button, Avatar} from '@material-ui/core';
import decode from 'jwt-decode';
import {useDispatch} from 'react-redux'

import { LOGOUT } from '../../constants/actionTypes';
import useStyles from  './styles'
import allMemoriesText from '../Images/ALL MEMORIES.svg';
import allMemoriesLogo from '../Images/logo.png';


const Navbar = () => {

  const classes = useStyles();
  const [user,setUser] = useState(JSON.parse(localStorage.getItem("profile")));
const dispatch = useDispatch()
 const history = useHistory()
const location = useLocation()

  const logout = ()=>{
      dispatch({type:LOGOUT});

      history.push('/')
      setUser(null);
  }

  useEffect(()=>{
    
    const token = user?.credential
    
    if(token){
      const decodedToken = decode(token);

      if(decodedToken.exp * 1000 < new Date().getTime()) logout();
    }

    setUser(JSON.parse(localStorage.getItem('profile')));

  },[location])
    return (
        <AppBar className={classes.appBar} position='static' color='inherit'>
          <Link to='/' className={classes.brandContainer}>
        <img src={allMemoriesText} alt='icon' height="45px"/>
        <img className={classes.image} src={allMemoriesLogo} alt='icon' height='40px'/>
        </Link>    
        <Toolbar className={classes.toolbar}>
           {user ?(<div className={classes.profile}>
                  <Avatar className={classes.purple} alt={user.result?.name} src={user.result?.picture}>{user.result?.name.charAt(0)}</Avatar>
                   <Typography className={classes.userName} variant='h6'>{user.result?.name}</Typography>
                   <Button variant="contained" className={classes.logout} color="secondary" onClick={logout}>Logout</Button>
           </div>):(
            <Button component={Link} to='/auth' variant='contained' color='primary'>Sign In</Button>
           )}
        </Toolbar>
    </AppBar>
    );

}

export default Navbar